import * as React from 'react';
import classnames from 'classnames';

interface IIconsProps {
    name: 'mail' | 'linkedin' | 'fb' | 'git';
    className?: string;
    size?: number
}

export const Icons = (props: IIconsProps) => {
    const { name, className, size } = props;
    let customStyle = size ? { width: size, height: size, } : {};
    let cls = classnames({
        'layout-icon-wrapper': true,
        [className]: className
    })
    return <img src={icons[name]} style={customStyle} className={cls} />
}

const icons = {
    mail: "data:image/svg+xml;base64,",
    linkedin: "data:image/svg+xml;base64,",
    fb: "data:image/svg+xml;base64,",
    git: "data:image/svg+xml;base64,"
}